<template>
  <div id="line_chart" style="height:400px;"></div>
</template>

<script>
import echarts from "echarts";
import visitApi from "@/api/visitApi";
export default {
  data() {
    return {};
  },
  mounted() {
    this.initLineChart();
    let that = this;
    let interval = setInterval(() => {
      this.initLineChart();
      if(that.$route.path != '/index'){
        clearInterval(interval)
      }
    }, 5000);
  },
  methods: {
    initLineChart: function() {
      var dom = document.getElementById("line_chart");
      if (!dom) {
        return false;
      }
      var myChart = echarts.init(dom);
      visitApi.count7Day().then(res => {
        let pvList = res.data.count7DayPVList;
        let uvList = res.data.count7DayUVList;
        let ipList = res.data.count7DayIPList;
        let dateList = [];
        let countPVList = [];
        let countUVList = [];
        let countIPList = [];
        pvList.map(o => {
          dateList.push(o.date);
          countPVList.push(o.count);
        });
        uvList.map(o => {
          countUVList.push(o.count);
        });
        ipList.map(o => {
          countIPList.push(o.count);
        });
        var option = {
          legend: {
            data: ["浏览量", "访问量", "独立IP"]
          },
          tooltip: {
            trigger: "axis"
          },
          title: {
            text: "最近7天访问曲线"
          },
          xAxis: {
            type: "category",
            data: dateList
          },
          yAxis: {
            type: "value"
          },
          series: [
            {
              name: "浏览量",
              data: countPVList,
              type: "line",
              smooth: true
            },
            {
              name: "访问量",
              data: countUVList,
              type: "line",
              smooth: true
            },
            {
              name: "独立IP",
              data: countIPList,
              type: "line",
              smooth: true
            }
          ]
        };
        myChart.setOption(option);
        window.addEventListener("resize", function() {
          myChart.resize();
        });
      });
    }
  }
};
</script>

